#new-project-dialog{
  width: 25vw;
  #cancel-button{
    background-color: $project-create-dlg-cancel-button-bg;
    margin-left: 1.5rem;
  }
  .content{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    label{
      flex-basis: 100%;
      flex-grow: 0;
      flex-shrink: 0;
      &#ptype, &#platform{
        flex-basis: 48%;
      }
    }
    .button-bar{
      flex-basis: 100%;
      margin: 1.5rem 0 2rem;
    }
  }
}

#main-content[current-page-marker='new-project']:before{
  background-image: url(../images/icons/boxes.svg);
}