#main-content[current-page-marker='explore-data']:before{
  background-image: url(../images/icons/compass.svg);
}
#main-content[current-page-marker='explore-data']{
  .content.explore-data{
    display: grid; 
    grid-template-rows: 1fr calc(100vh - 8.6rem - 4rem);
    grid-template-columns: 1fr;
    justify-content: start;
    align-content: start;
    flex-direction: column; 
    z-index: 1;
    width: var(--rhs-width);
  }
}

.content.explore-data{
  .action-bar, #explore-summary-tabs-content-container{
    margin: 0 2rem;
    width: calc(var(--rhs-width) - 4rem);
  }
  #explore-summary-tabs-content-container{
    display: flex;
  }
  .action-bar{
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    select{
      padding: 0.5rem;
      background-color: $orth-light;
      font-size: 1rem;
      border-radius: 4px;
      border-color: $orth-light;
      color: $orth-dark;
      margin-left: 0.5rem;
      flex-grow: 0;
    }
    label.compact input{
      color: $content-fg;
    }
    .spacer{flex-grow: 1;}
    button{
      justify-self: flex-end;
      flex-grow: 0;
    }
  }
  .table-container {
    position: relative;
    max-width: calc(var(--rhs-width) - 4rem);
    .action-bar{
      position: absolute;
      left: 25rem;
      top: -2.5rem;
      width: calc(100% - 28rem);
      label.compact{
        padding: 0;
        min-width: unset;
        align-self: stretch;
        width: 2.3rem;
        border-radius: 0.4rem;
        span{
          background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.54), rgba(255, 255, 255, 0.53)), url(../images/icons/search.svg);
          background-size: 16px;
          background-repeat: no-repeat;
          background-position: left top;
          padding-left: 20px;
          height: 20px;
        }
      }
      button.saveAsExcel{
        padding: 1rem;
        min-width: 2rem;
        background-image: url(../images/icons/table.svg);
        background-size: 50%;
        background-repeat: no-repeat;
        background-position: center;
      }
    }
  }

  #explore-summary-tabs-content-container{
    th {
      background-color: transparent;
      color: $table-header-fg;
      text-align: left;
      vertical-align: middle;
      cursor: pointer;
      &.feature{
        background-color: #141619;
      }
      &.sort{
        background-image: url("../images/icons/sort.svg");
        background-size: 11px auto;
        background-repeat: no-repeat;
        background-position: right top;
        &.asc{
          background-image: url("../images/icons/sort-asc.svg")
        }
        &.desc{
          background-image: url("../images/icons/sort-desc.svg")
        }
      }
      &:nth-child(2n) {
        background-image: linear-gradient(to top, rgba(255, 255, 255, 0.07), rgba(255, 255, 255, 0));
        &.sort{
          background-image: url("../images/icons/sort.svg"), linear-gradient(to top, rgba(255, 255, 255, 0.07), rgba(255, 255, 255, 0));
          background-size: 11px auto, 100%;
          background-position: right top, left center;
          &.asc{
            background-image: url("../images/icons/sort-asc.svg"), linear-gradient(to top, rgba(255, 255, 255, 0.07), rgba(255, 255, 255, 0));
          }
          &.desc{
            background-image: url("../images/icons/sort-desc.svg"), linear-gradient(to top, rgba(255, 255, 255, 0.07), rgba(255, 255, 255, 0));
          }
        }
      }
    }
    td {
      color: white;//$table-body-fg;
      background-color: darken($orth-main, 10%);//$table-body-bg;
      text-align: left;
      vertical-align: middle;
      border-top: 2px solid $orth-main;//$table-body-cell-border-color;
      transition: background-color 0.2s ease-out;
      &.number{
        font-family: Inconsolata, "Lucida Sans Typewriter", Menlo;
      }
    }
    thead tr{
      background-color: $table-header-bg;
      background-image: linear-gradient(to bottom right, transparentize(black, 1), transparentize(black, 0.1) 80%);
    }
    tbody tr{
      background-color: darken($orth-main, 10%);//$table-body-bg;
      transition: background-color 0.2s ease-out;
      &:hover{
        background-color: darken($orth-main, 20%);//darken($table-body-bg, 5%);
        td{
          background-color: darken($orth-main, 20%);//darken($table-body-bg, 5%);
        }
      }
    }
    tbody td{
      &:nth-child(2n) {
        background-color:  darken($orth-main, 15%);
      }
    }
    #explore-data-numerical-data{
      td{
        text-align: right;
      }
      td:nth-child(1){
        text-align: left;
      }

    }
  }
  .tabsTriggersContainer{
    margin-top: 2rem;
    margin-left: 10rem;
    width: 16rem;
    display: flex;
    justify-content: space-between;
  }
  .tab-trigger{
    padding: 0.8rem 0.9rem 0.6rem;
    color: $orth-dark;
    transform: translateY(1px);
    background-color: transparentize($primary-light, 0.5);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    text-transform: uppercase;
    font-size: 0.8rem;
    &:hover{
      background-color: transparentize($primary-main, 0.3);
    }
    &.selected{
      background-color: #121216;
      color: transparentize($primary-light, 0.15);
      font-weight: 600;
      &:hover{
        background-image: linear-gradient(to top, #121216, lighten(#121216, 10%));
      }
    }
  }
}
.explore-data-table-container{
  @extend %fixed-table-container;
}
#explore-data-catgorical-data .explore-data-table-container table{
  min-width: 45rem;
}