#main-content[current-page-marker='md_score-models']:before{
  background-image: url('../images/icons/bullseye.svg');
}
#main-content[current-page-marker='md_score-models']{
  .content.md-score-models{
    display: flex; 
    flex-direction: row;
    justify-content: stretch;
    align-content: stretch;
    z-index: 1;
    height: calc(100vh - 8.6rem);
  }
}

#score-models-main{width: 100%;}
#score-models{
  
  .searchBar{
    margin: 1rem 0 0 2rem;
    display: flex;
    align-items: flex-end;
    select{
      &[not-empty], &[data-source-count="0"]{
        option.no-data{display: none;}
      }
    }
    button{
      margin: 0 1rem 0rem;
      padding: 0.5em 0.7em;
      font-size: 1rem;
      border: 0;
      background-color: $primary-light;
      color: white;
      line-height: 1.2rem;
    }
  }
   h3{
     text-transform: uppercase;
     font-size: 1.2rem;
     font-weight: 900;
     @include set-font;
     color: $primary-light;
     margin: 2rem 0 1rem 2rem;
   }
   p{
     margin: 1rem 0 1rem 2rem;
   }
  .tableContainer {
    margin: 1rem 2rem 0 2rem;
    h4{
      background-color: $mp-table-caption-bg;
      color: $mp-table-caption-fg;
      padding: 1.5rem 1.3rem;
      font-weight: 600;
    }
    table{
      min-width: 60rem;
      border-spacing: 0 5px;
      border-collapse: separate;
      th{
        background-color: $mp-table-caption-bg;
        color: $mp-table-caption-fg;
        padding: 1rem;
        text-align: left;
        &.name{
          text-align: left;
        }
      }
      tr.trophy{
        td{
          background-color: $secondary-light;
          color: white;
          &.dPath{
            background-size: calc(var(--icon-size) - .8rem);
            background-position: 83% calc((3.7rem - var(--icon-size)) / 2);
            background-image: url('../images/icons/trophy.svg');
            a{
              background-image: url('../images/icons/cloud-download-grey.svg');
            }
          }
        }
      }
      tr.selected td{
        background-color: $primary-light;
        color: white;
        &.dPath{
          &:after{
            color: $mp-separator-bg;
            transform: translate(2px, calc(var(--table-row-height) / 2 - 6px))  scaley(1.5);
          }
          a{
            background-image: url('../images/icons/cloud-download-grey.svg');
          }
        }

      }
      tr.template-row{
        display: none;
        td {
          display: none;
        }
      }
      tr[status="completed"]{
        td.progress{display: none;}
      }
      tr[status="running"]{
        td.auc, td.acc, td.gini, td.ks, td.dPath{display: none;}
      }
      td{
        --icon-size: 2.5rem;
        padding: 1rem;
        &.name{
          padding: 1rem 1rem 1rem 4rem;
        }

        background-size: var(--icon-size);
        background-position: calc((4rem - var(--icon-size)) / 2);
        background-repeat: no-repeat;
        background-color: transparentize($mp-row-bg, 0.2);
        position: relative;
        transition: all 0.3s ease-out;
        &.type-spark{
          background-image: url('../images/icons/spark.svg');
        }
        &.type-python{
          background-image: url('../images/icons/python.svg');
        }
        &.type-h2o{
          background-image: url('../images/icons/h2o.svg');
        }
        &.dPath{
          &:after{
            content: '◀';
            display: block;
            color: $mp-separator-bg;
            transition: color 0.3s ease-out, transform 0.3s ease-out;
            position: absolute;
            right: -2rem;
            top: 0px;
            transform: translate(2px, calc(var(--table-row-height) / 2 - 6px)) scaley(0);
          }
          a{
            width: var(--icon-size);
            height: var(--icon-size);
            display: block;
            background-image: url(../images/icons/cloud-download.svg);
            position: absolute;
            left: calc(var(--icon-size) *1.5);
            top: calc( (var(--table-row-height) - var(--icon-size)) / 2 + 5px);
            --icon-size: 1.7rem;
            background-repeat: no-repeat;
          }
        }
        &.number{
          font-family: Inconsolata, "Lucida Sans Typewriter", Menlo;
        }
        &.progress {
          padding: 0 2rem;

          .progress-outer {display: block;background-color: $orth-light;height: 0.9rem;}
          .progress-inner {
            height: 0.9rem;
            display: block;
            width: calc(var(--progress-value) * 1%);
            transition: width 0.5s ease-in-out;
            background-color: $primary-light;
          }
        }
      }
    }
  }

}