#main-content[current-page-marker='validate-features']:before{
  background-image: url(../images/icons/check-circle-o.svg);
}
#main-content[current-page-marker='validate-features']{
  .content.validate-features{
    display: grid; 
    grid-template-rows: 1fr calc(100vh - 8.6rem - 4rem);
    grid-template-columns: 1fr;
    justify-content: start;
    align-content: start;
    flex-direction: column; 
    z-index: 1;
  }
}

.content.validate-features{
  .action-bar, #features-table-container{
    margin: 0 2rem;
  }
  .action-bar{
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: stretch;
    align-items: center;
    select{
      padding: 0.5rem;
      background-color: $orth-light;
      font-size: 1rem;
      border-radius: 4px;
      border-color: $orth-light;
      color: $orth-dark;
      margin-left: 0.5rem;
      flex-grow: 0;
    }
    button{
      flex-grow: 0;
      margin-left: 1rem;
    }
  }
  #features-table-container{
    table{
      width: 54rem;
    }
    td, th{
      height: 2.5rem;
      font-size: 0.9rem;
      .on-off{
        height: calc(var(--base-size) * 2);
      }
      &.select div{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
      }
    }
    th {
      background-color: transparent;
      color: $table-header-fg;
      text-align: left;
      vertical-align: middle;
      &.featureName, &.featureType{
        cursor: pointer;
      }
    }
    td {
      color: $table-body-fg;
      background-color: $table-body-bg;
      text-align: left;
      vertical-align: middle;
      border-top: 3px solid $table-body-cell-border-color;
      transition: background-color 0.2s ease-out;
    }
    thead tr{
      background-color: $table-header-bg;
      background-image: linear-gradient(to bottom right, transparentize(black, 1), transparentize(black, 0.1) 80%);
    }
    tbody tr{
      background-color: $table-body-bg;
      transition: background-color 0.2s ease-out;
      &:hover{
        background-color: darken($table-body-bg, 5%);
        td{
          background-color: darken($table-body-bg, 5%);
          display: flex;
          flex-direction: row;
          justify-content: flex-start;
          align-items: center;
        }
      }
    }
    tbody td{
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
    }
    thead, tbody{
      tr{
        grid-template-columns: 3rem 20rem 10rem 20rem;
      }
    }
    label.on-of{
      border: 1px solid $on-off-border-color;
    }
    td.actions div{
      width: 15rem;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      height: 100%;
    }
    th.role{
      width: 15rem;
      padding-left: 0.7rem;

    }
  }
}