.split-button{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: $primary-light;
  position: relative;
  margin: 0 1rem;
  .dropdown-trigger-input{display: none;}
  .dropdown-content{
    button{
      display: none;
      margin: 0!important;
      white-space: nowrap;
      &.default{
        display: block;
      }
    }
  }
  .dropdown-trigger-input:checked, .dropdown-trigger-input.checked {
    &~.dropdown-content{
      display: flex;
      flex-direction: column;
      background-color: $primary-light;
      button:not(.default){
        display: block;
        position: absolute;
        left: 0;
        @for $i from 2 through 10 {
          &:nth-child(#{$i}){
            top: ($i - 1) * 100%;
          }
        }
      }
    }
  }
  .dropdown-trigger{
    background-image: url(../images/icons/down-triangle.svg);
    flex-basis: .5rem;
    padding: 1rem;
    height: 100%;
    background-position: center;
    background-size: 0.9rem;
    background-repeat: no-repeat;
    cursor: pointer;
  }
}