%icon-and-text-button{
  background-color: $button-highlight-bg;
  color: $grey-lightest;
  border: 0;
  line-height: 2rem;
  min-width: 6rem;
  padding: 0 0.7rem;
  cursor: pointer;
  font-family: Montserrat, Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1rem;
  transition: background-color 0.1s ease-in-out;
  position: relative;
  outline: none;
  overflow: hidden;
  &[disabled]{
    background-color: $button-disabled-bg;
    cursor: not-allowed;
  }
  &:after{
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: transparentize(black, 0.95);
    transform: scaleX(1);
    transform-origin: 100% 0;
    transition: transform 0.2s ease-in-out;
  }
  &:hover, &:focus, &:active{
    &:after{
      transform: scaleX(0);
    }
  }
  &.iconAndText{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.7rem 0 1rem;
    svg{
      width: 1.4rem;
      height: 1.4rem;
      margin-left: .5rem;
      path { fill: white; }
    }
  }
}

.button-bar button{
  @extend %icon-and-text-button;
}

.on-off {
  --base-size: .6rem;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  position: relative;
  padding-left: calc(var(--base-size) * 3);
  line-height: calc(var(--base-size) * 2);
  cursor: pointer;
  input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    &:checked{
      & ~ .indicator {
        background: $on-off-highlight;
        &:after {
          display: block;
        }
      }
    } 
    &:disabled ~ .indicator {
      background: $on-off-disabled-bg;;
      opacity: 0.6;
      pointer-events: none;
    }
  }
  .indicator {
    position: absolute;
    top: 0;
    left: 0;
    height: calc(var(--base-size) * 2);
    width: calc(var(--base-size) * 2);
    background: $on-off-bg;
    &:after {
      content: '';
      position: absolute;
      display: none;
    }
  }
  &:hover input ~ .indicator, input:focus ~ .indicator{
    background: $on-off-hover;
  }
  &:hover input:not([disabled]):checked ~ .indicator,
  input:checked:focus ~ .indicator {
    background: $on-off-highlight;
  }
  &.has-radio{
    .indicator {
      border-radius: 50%;
      &:after {
        left: calc(var(--base-size) * .7);
        top: calc(var(--base-size) * .7);
        height: calc(var(--base-size) * .6);
        width: calc(var(--base-size) * .6);
        border-radius: 50%;
        background: #fff;
      }
    }
    input:disabled ~ .indicator:after {
      background: #7b7b7b;
    }
  } 
  &.has-checkbox{
    .indicator:after {
      left: calc(var(--base-size) * .8);
      top: calc(var(--base-size) * .45);
      width: calc(var(--base-size) * .3);
      height: calc(var(--base-size) * .8);
      border: solid #fff;
      border-width: 0 calc(var(--base-size) * .2) calc(var(--base-size) * .2) 0;
      transform: rotate(45deg);
    }
    input:disabled ~ .indicator:after {
      border-color: #7b7b7b;
    }
  } 
}

label.toggle-button{
  display: block;
  position: relative;
  overflow: hidden;
  height: 2rem;
  input{
    position: absolute;
    top: -2rem;
    opacity: 0;
  }
  span{
    padding: 0.5rem 0.7rem;
    color: $toggle-button-off-fg;
    background-color: $toggle-button-off-bg;
    transition: all 0.2s ease-in-out;
    display: inline-block;
    border: 1px solid $toggle-button-off-bg;
    cursor: pointer;
  }
  input:checked ~ span{
    color: $toggle-button-on-fg;
    font-weight: bold;
    background-color: $toggle-button-on-bg;
    border: 1px solid $toggle-button-on-bg;
    border-radius: 0.3rem;
  }
    
}


