#main-content[current-page-marker='mp_model-comparison']:before{
  background-image: url('../images/icons/box-select.svg');
}
#main-content[current-page-marker='mp_model-comparison']{
  .content.ml-leaderboard{
    display: none; 
  }
  .content.mp-model-comparison{
    display: flex; 
    flex-direction: row;
    justify-content: stretch;
    align-content: stretch;
    z-index: 1;
    height: calc(100vh - 8.6rem);
  }
}

#model-comparison{
  .searchBar{
    margin: 1rem 0 0 2rem;
    select{
      @include set-font;
      font-size: 1rem;
    }
    label{
      @include set-font;
      font-size: 0.9rem;
      display: block;
      span{
        margin-right: 1rem;
      }
    }
  }
  .tableContainer {
    margin: 1rem 2rem 0 2rem;
    h4{
      background-color: $mp-table-caption-bg;
      color: $mp-table-caption-fg;
      padding: 1.5rem 1.3rem;
      font-weight: 600;
    }
    table{
      width: 100%;
      max-width: 60rem;
      border-spacing: 0 5px;
      border-collapse: separate;
      th{
        background-color: $mp-table-caption-bg;
        color: $mp-table-caption-fg;
        padding: 1rem;
        text-align: left;
        &.name{
          text-align: left;
        }
      }
      tr.trophy{
        td{
          background-color:$mp-selected-row-bg;
          color: $mp-selected-row-fg;
          &.br_s{
            background-image: url('../images/icons/trophy.svg');
            background-position-x: 80%;
            background-size: 20%;
          }
        }
      }
      tr.template-row{
        display: none;
      }
      td{
        --icon-size: 2.5rem;
        padding: 1rem;
        font-family: Inconsolata, "Lucida Sans Typewriter", Menlo;
        &.name{
          padding: 1rem 1rem 1rem 4rem;
          color: $mp-row-fg;
          @include set-font;
        }

        background-size: var(--icon-size);
        background-position: calc((4rem - var(--icon-size)) / 2);
        background-repeat: no-repeat;
        background-color: $mp-row-bg;
        position: relative;
        transition: all 0.3s ease-out;
        &.type-spark{
          background-image: url('../images/icons/spark.svg');
        }
        &.type-python{
          background-image: url('../images/icons/python.svg');
        }
        &.type-h2o{
          background-image: url('../images/icons/h2o.svg');
        }
      }
      &[data-active-source='test'] tr.train{
        display: none;
      }
      &[data-active-source='train'] tr.test{
        display: none;
      }
    }
  }

}