#main-content.dashboard{
  overflow: auto;
  --no-of-projects: 4;
  --no-of-columns: 4;
  --no-of-rows: 2;
  @include media(">=desktop"){
    --no-of-columns: 4;
    --no-of-rows: calc(minmax(2, var(--no-of-projects) / var(--no-of-columns)));
  }
  .dashboardContent{
    display: grid;
    grid-template-columns: repeat( var(--no-of-columns), 1fr);
    grid-template-rows: repeat(var(--no-of-rows), min-content);
    grid-gap: 4rem;
    margin: 2rem;
  }
  #main-nav.pinned ~ main &{
    --no-of-columns: 3;
  }
  
}

#main-content[current-page-marker='dashboard']:before{
  background-image: url(../images/icons/boxes.svg);
}
#main-content[current-page-marker='dashboard'], #main-content[current-page-marker='new-project'], #main-content[current-page-marker='db-connection'], #main-content[current-page-marker='file-upload']{
  .content.dashboard{display: flex; flex-direction: column;}
}
.button-bar.create-project{
  margin: 2rem;
  button{
    padding: 0.5rem 1.2rem 0.5rem .9rem;
  }
}
.box.project-box{
  background-color: $dashboard-project-box-bg;
  --icon-width: 1.2rem;
  min-width: auto;
  max-width: auto;
  min-height: auto;
  .title{
    margin-top: 1rem;
  }
  .description{
    margin-top: 1rem;
    font-weight: 100;
  }
  dl{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    &>div{
      flex-basis: 48%;
      display: grid;
      grid-template-columns: var(--icon-width) 1fr;
      grid-template-rows: var(--icon-width);
      grid-column-gap: calc(var(--icon-width) / 2);
      align-items: center;
      margin: 0.7rem 0;
    }
    dt{
      svg{
        width: var(--icon-width);
        path{fill: $dashboard-project-icon-fg}
      }
    }
    dd{
      word-wrap: break-word;
      font-weight: 100;
    }
  }
  .button-bar{
    margin: 2rem 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    button.only-icon{
      background-color: transparent;
      color: $dashboard-project-icon-fg;
      border: 0;
      line-height: 0.7rem;
      min-width: unset;
      padding: 0;
      cursor: pointer;
      font-size: 0.7rem;
      transition: none;
      position: unset;
      outline: none;
      overflow: hidden;
      z-index: 1;
      &:after{display: none;}
      &:hover, &:focus{
        svg path{fill: $dashboard-project-icon-highlight-fg;}
        &+.project-actions-menu{display: flex;}
      }
      svg{
        width: 1rem; height: 1rem;
        path{fill: $dashboard-project-icon-fg;}
      }
      &+.project-actions-menu{
        display: none;
        z-index: 1;
        &:hover{
          display: flex;
        }
        button{
          margin-right: 0.5rem;
        }
      }
      
    }
    .spacer{
      flex-grow:1;
      height: 100%;
    }
    button.exploreProject{
      justify-self: flex-end;
    }
  }
  .project-actions-menu{
    padding-left: 0.5rem;
  }
}
