#login.box{
  min-width: 20vw;
  .on-off{
    margin-top: 2rem;
  }
  .button-bar{
    margin-top: 1rem;
    margin-bottom: 2rem;
  }
  .content{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;

  }
  .errorBar{
    font-size: 0.8rem;
    margin: 1rem 0 0;
    color: adjust-color($color: $message-bar-error-bg, $lightness: 10%);
    text-align: center;
  }
}

#main-content[current-page-marker='login']:before{
  background-image: url(../images/icons/lock.svg);
}