#main-content[current-page-marker^='mp']:before{
  background-image: url('../images/icons/line-chart.svg');
}
#main-content[current-page-marker^='mp']{
  .content.ml-leaderboard{
    display: flex; 
    flex-direction: row;
    justify-content: stretch;
    align-content: stretch;
    z-index: 1;
    height: calc(100vh - 8.6rem);
  }
}

#ml-leaderboard{
  --graph-area-width: 50vw;
  --graph-area-delta : 0;
  display: grid;
  grid-template-rows: 100%;
  grid-template-columns: minmax(min-content, 1fr) 10px calc(var(--graph-area-width) - (var(--graph-area-delta) * 1px));
  width: 100%;

}
#leaderboard .separator{
  background-color: $mp-separator-bg;
  position: relative;
  cursor: col-resize;
}
#graph-area{
  background-color: $mp-graph-area-bg;
  height:100%;
  overflow: auto;
  .graphs-outer-area{
    display: none;
    &.active{
      display: block;
    }
    h4{
      color: $primary-main;
      font-size: 1.1rem;
      text-transform: uppercase;
      font-weight: 600;
      background-color: transparentize(white, 0.95);
      padding: 7px 10px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .graphs-area{
      display: flex;
      flex-direction: row;
      justify-content: stretch;
      align-items: flex-start;
      flex-wrap: wrap;
    }
    .graph-outer-container{
      @extend %aspect-ratio-box;
      flex-basis: calc(50% - 40px);
      &#calibrationChart{
        flex-basis: calc(100% - 40px);
      }
      margin: 20px;
      flex-shrink: 0;
    }
    &#fprAndPA{
      &.active{
        display: flex;
      }
      height: calc(100% - 40px);
      flex-direction: row;
      justify-content: stretch;
      align-items: flex-start;
      .graph-outer-container{
        flex-basis: calc(30% - 40px);
        margin: 0px;
        flex-shrink: 0;
        flex-grow: 0;
        &:before{display: none;}
        &#confusionMatrix{
          flex-basis: 100px;
          align-self: flex-start;
        }
      }
      .graphs-area{
        display: flex;
        flex-direction: column;
        justify-content: stretch;
        align-items: stretch;
        flex-wrap: nowrap;
        flex-basis: 45%;
        flex-shrink: 0;
        flex-grow: 1;
        margin: 20px;
        overflow: auto;
        height: 100%;
      }
      #probLevels{
        --table-row-height: 1.5rem;
        --table-col-width: 5.5rem;
        --table-no-cols: 5;
        height: calc(100% - 30px);
        display: flex;
        flex-direction: column;
        flex-shrink: 1;
        margin: 20px;
        margin-right: 0px;
        #probLevelsTableDiv{
          height: 100%;
          border: 1px solid rgba(255, 255, 255, 0.05);
          border-top: 0;
          background-color: rgba(6, 55, 112, 0.05);
        }
        table{
          border: 0px;
          font-size: 0.8rem;
          height: 100%;
          tbody{overflow-x: hidden;}
          tr:nth-child(2n) td{
            background-color: transparentize($primary-dark, 0.85);
            pointer-events: all;
          }
          th{
            text-align: left;
            padding: 4px;
            font-weight: 400;
            color: transparentize($color: $primary-main, $amount: 0.3);
            background-color: $orth-dark;
          }
          td{
            cursor: pointer;
            font-family: Inconsolata, "Lucida Sans Typewriter", Menlo;
            font-size: .9rem;
            padding: 4px;
            color: transparentize(white, 0.3);
            &.float{text-align: right;padding-right: 2.2em;}
          }
          tr.selected td{
            background-color: transparentize($primary-light, 0.4);
          }
        }
      }
      #precisionGraph{
        margin-bottom: 10px;
        margin-right: 20px;
      }
      #confusionMatrix{
        margin: 10px 20px 10px 0;
        .confusion-matrix-tables{
          display: flex;
          flex-direction: row;
          justify-content: flex-start;
          align-items: flex-start;
          border: 1px solid transparentize(white, 0.95);
          border-top: 0;
          background-color:transparentize($primary-dark, 0.95);
        }
        table {
          font-size: 0.8rem;
          td, th{
            padding: 6px 2px;
          }
          th{
            color: transparentize($color: $primary-main, $amount: 0.3);
            background-color: $orth-dark;
          }
          td{
            font-family: Inconsolata, "Lucida Sans Typewriter", Menlo;
            color: transparentize(white, 0.3);
          }
          th.actual{
            position: relative;
            width: 1rem;
            span{
              display: block;
              transform: rotatez(-90deg) translatex(-1rem);
            }
          }
          th.fraud, th.nonfraud, th.total{text-align: left;}
          th.cf1, th.predicted{padding-bottom: 0px;}
          .nf-nf, .tot-nf{background-color: transparentize($primary-dark, 0.7);}
          .f-nf{background-color: transparentize($primary-lightest, 0.7);}
          .nf-f, .tot-f{background-color: transparentize(darken($primary-dark, 10%), 0.7);}
          .f-f{background-color: transparentize(darken($primary-lightest, 10%), 0.7);}
          .nf-tot, .tot-tot{background-color: transparentize(darken($primary-dark, 20%), 0.7);}
          .f-tot{background-color: transparentize(darken($primary-lightest, 20%), 0.7);}
          
          &#confTable1{
            // tr:nth-child(2n) td{
            //   background-color: transparentize($primary-dark, 0.85);
            // }
          }
          &#confTable2{
            margin-top: 1.15rem;
            margin-left: 1rem;
            td{padding-left: 4px;}
            tr:nth-child(2n+1) td{
              background-color: transparentize($primary-dark, 0.85);
            }
          }
        }

      }
      #ogiveCurve{
        margin-top: 10px;
        margin-right: 20px;
      }
    }
  }
}
#leaderboard {
  .searchBar{
    margin: 1rem 0 0 2rem;
    select{
      @include set-font;
      font-size: 1rem;
    }
    label{
      @include set-font;
      font-size: 0.9rem;
      display: block;
      span{
        margin-right: 1rem;
      }
    }
  }

  .tableContainer {
    margin: 1rem 2rem 0 2rem;
    h4{
      background-color: $mp-table-caption-bg;
      color: $mp-table-caption-fg;
      padding: 1.5rem 1.3rem;
      font-weight: 600;
    }
    table{
      width: 100%;
      border-spacing: 0 5px;
      border-collapse: separate;
    td{
        --icon-size: 2.5rem;
        padding: 0 1rem 0 4rem;
        background-size: var(--icon-size);
        background-position: calc((4rem - var(--icon-size)) / 2);
        background-repeat: no-repeat;
        background-color: $mp-row-bg;
        position: relative;
        cursor: pointer;
        transition: all 0.3s ease-out;
        &.selected{
          background-color: $mp-selected-row-bg;
          a{color: $mp-selected-row-fg;}
        }
        &.type-spark{
          background-image: url('../images/icons/spark.svg');
        }
        &.type-python{
          background-image: url('../images/icons/python.svg');
        }
        &.type-h2o{
          background-image: url('../images/icons/h2o.svg');
        }
        @include link{
          // color: $mp-row-fg;
          text-decoration: none;
        }
        a{
          color: $mp-row-fg;
          padding: 1.5rem 0;
          display: block;
        }
        &:after{
          content: '◀';
          display: block;
          color: $mp-separator-bg;
          transition: color 0.3s ease-out, transform 0.3s ease-out;
          position: absolute;
          right: -2rem;
          top: 0px;
          transform: translate(2px, 1.5rem) scaley(0);
        }
        &.selected:after{
          color: $mp-separator-bg;
          transform: translate(2px, 1.5rem) scaley(1.5);
        }
      }
    }
  }
  .graph-container{
    padding: 10px;
    border: 1px solid transparentize(white, 0.95);
    border-top: 0;
    background-color:transparentize($primary-dark, 0.95);
    .c3-axis, .discriminationAxis{
      path, line{
        stroke: $primary-light;
      }
      text{
        fill: transparentize($grey-lightest, .2);
      }
    }
    .discriminationTitle{
      fill: $primary-light;
    }
    .c3-legend-item text{
      fill: transparentize($grey-lightest, .2);
    }
    .c3-line{
      stroke-width: 2px;
    }
    .c3-line-Reference{
      stroke-dasharray: 5, 5;
      stroke: transparentize($primary-main, 0.6) !important;
    }
    .c3-grid{
      line{
        stroke: transparentize($primary-main, 0.8) !important;
      }
      .c3-xgrid-line{
        line{
          stroke: transparentize($primary-main, 0.6) !important;
        }
        &.ks-score text{
          transform: translatey(10px) translateX(37px) rotate(0deg);
        }
      }
    } 
    .c3-circles-Reference{display: none;}

    .graph svg{
      width: 100%;
      height: 100%;
    }
  }
}
