label.compact{
  --line-height: 1.1rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.6rem 1fr;
  margin-top: var(--line-height);
  &:first-child{
    margin-top: 0;
  }
  @include set-font-secondary;
  span, input, textarea, select{
    grid-row: 2/-1;
    grid-column: 1/-1;
    @include set-font-secondary;
  }
  span{
    z-index:1;
    font-size: var(--line-height);
    color: $compact-input-placeholder-color;
    transition: transform 0.1s ease-in-out;
    margin-left: 0.1rem;
    cursor: text;
    background-color: inherit;
    display: flex;
    align-items: center;
    transform-origin:0 0;
    display: flex;
    align-items: flex-start;
    &.errorMsg{
      display: none;
    }
  }
  select~span{
    position: relative;
    &:after{
      content: '▾';
      display: block;
      position: absolute;
      right: .25rem;
      font-size: 0.8rem;
      color: $compact-input-fg;;
      top: .5rem;
    }
  }
  
  input, textarea, select{
    background: transparent;
    padding: 0.2rem 0.5rem 0 0.1rem;
    border: 0;
    border-bottom: 1px solid $compact-input-line-color;
    color: transparent;
    outline: none;
    font-size: var(--line-height);
    &::placeholder{
      color: transparent;
      transition: color 0.1s ease-in-out;
    }
    &:focus, &[not-empty]{
      color: $compact-input-fg;
      &::placeholder{
        color: $compact-input-placeholder-color;
      }
      grid-row: 2/span 1;
      &~span{
        grid-row:1/span 1;
        transform: scale(0.7);
        cursor: default;
        &:after{
          display: none;
        }
      }
    }
    &[pattern][not-empty]{
      &:invalid, &.invalid{
        border-bottom: 1px solid $secondary-dark;
        & ~ span.errorMsg{
          display: flex;
          grid-row: 2/span 1;
          align-self: end;
          transform: scale(0.7) translateY(150%);
          color: $secondary-dark;
          justify-self: right;
          text-align: right;
          transform-origin: right center;
        }
      }
    }
  }
  select:focus{
    background-color: $header-bg;
  }
  textarea{
    --ta-lh: var(--line-height);
    background: repeating-linear-gradient(to bottom, transparent 0, transparent calc(var(--ta-lh) - 1px), $compact-textarea-inner-line-color calc(var(--ta-lh) - 1px), $compact-textarea-inner-line-color var(--ta-lh));
    line-height: var(--ta-lh);
    @for $i from 1 through 10{
      &[rows="#{$i}"]{
        height: calc( var(--ta-lh) * #{$i} - #{$i}px);
      }
    }
  
  }
}

label.compact.light{
  span{
    color: $light-compact-input-placeholder-color;
    background-color: inherit;
  }
  select~span:after{
    color: $light-compact-input-fg;;
  }
  input, textarea, select{
    background: transparent;
    border-bottom: 1px solid $light-compact-input-line-color;
    color: transparent;
    &::placeholder{
      color: transparent;
      transition: color 0.1s ease-in-out;
    }
    &:focus, &[not-empty]{
      color: $light-compact-input-fg;
      &::placeholder{
        color: $light-compact-input-placeholder-color;
      }
    }
    &[pattern][not-empty]{
      &:invalid, &.invalid{
        border-bottom: 1px solid $secondary-dark;
        & ~ span.errorMsg{
          color: $secondary-dark;
        }
      }
    }
  }
  select:focus{
    background-color: transparent;
  }
  textarea{
    background: repeating-linear-gradient(to bottom, transparent 0, transparent calc(var(--ta-lh) - 1px), $light-compact-textarea-inner-line-color calc(var(--ta-lh) - 1px), $light-compact-textarea-inner-line-color var(--ta-lh));
  }
}