#main-content[current-page-marker="ae-automodel"]{
  background-color: $ae-bg;
  color: $ae-fg;
  &:before{
    background-image: url('../images/icons/cubes.svg');
    opacity: 0.02;
  }
  .content.ae-automodel{
    display: flex; 
    flex-direction: row;
    justify-content: stretch;
    align-items: stretch;
    flex-direction: row; 
    z-index: 1;
    
  }
}
%dial{
  display: grid;
  align-items: center;
  justify-items: center;
}
%dial-text{
  font-size: 0.8em;
  grid-column: 1/span 1;
  grid-row: 1/span 1;
  transform: translateY(31%) translateX(2%);
}
%dial-svg{
  position: relative;
  grid-column: 1/span 1;
  grid-row: 1/span 1;
  overflow: visible;
  width: 100%;
  ellipse{
    animation-name: heartbeatsvg;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
}
#model-development{
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: 50vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  overflow: auto;
  max-height: calc(100vh - 8.6rem);
  width: 100%;
  .stage{
    display: none;
    max-width: 60vw;
  }
  .stage.current{display: block;}
  .highlight{
    text-transform: uppercase;
    color: $ae-highlight-fg;
    @include set-font-secondary;
    font-weight: 600;
    font-size: 1.2rem;
  }

  #unstarted{
    display: grid;
    margin: 0 1rem;
    grid-template-columns: 2fr 1fr 1fr;
    grid-column-gap: 5rem;
    grid-template-rows: repeat(4, 3rem) 1fr 5rem;
    grid-row-gap: 2rem;
    justify-content: start;
    align-content: end;
    align-items: end;
    grid-auto-flow: dense;
    h3{
      grid-row: 1/span 1;
      grid-column: 2/span 2;
      &:first-child{grid-column: 1/span 1;}
    }
    label.compact{
      grid-column: 1/span 1;
      margin-top: 0;
    }
    dl.summary{
      display: grid;
      grid-column: 2/span 2;
      grid-row: 2/span 3;
      grid-row-gap: 2rem;
      grid-column-gap: 5rem;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, 3rem);
      dd,dt{align-self: end;}
      dt{grid-column: 1/span 1;}
      dd{
        grid-column: 2/span 1;
        @extend .highlight;
      }
    }
    .logo svg{width: 1.7rem; transform: translateY(0.3rem);}
    #start-ae{
      background-color: transparent;
      grid-column: 1/-1;
      grid-row: 5/span 1;
      border: 0;
      outline: 0;
      cursor: pointer;
      width: 9rem;
      justify-self: center;
      padding: 0;
      margin: 0;
      svg{
        width: 9rem;
        transition: transform 0.1s ease-in;
        &:hover{
          transform: perspective(300px) scale(1.02) rotatex(-4deg);
        }
      }
      &:active svg:hover{
        transform: perspective(200px) scale(1) rotatex(-2deg);
      }
    }
    .progress.group{
      grid-column: 1/span 3;
      grid-row: 6/span 1;
      display: grid;
      grid-template-columns: repeat(4,1fr 3.7rem);
      grid-template-rows: 5rem;
      grid-column-gap: 3rem;
      grid-auto-flow: column;
      align-items: center;
      align-content: center;
      @for $i from 1 through 8{
        &>*:nth-child(#{$i}){
          grid-column: #{$i}/span 1;
        }
      }
      
      
    }
  }
  #started{
    display:none;
    grid-template-columns: 1fr 20rem 1fr;
    grid-template-rows: 3rem 20rem 24rem;
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    width: 100%;
    margin: 0 1rem;
    span.logo svg{
      width: 1.8rem;
      transform: translatey(5px);
      margin-right: 0.2rem;
    }
    h3#runtime{
      grid-column: 1/-1;
      justify-self: center;
      margin-top: 1rem;
    }
    #completed-panel-1{
      grid-column: 1/span 1;
    }
    #completed-panel-2{
      grid-column: 3/span 1;
      margin-left: 1rem;
      h4.FeatureSelection+table{
        background-image: none;
      }
    }
    .completed-panel{
      display: none;
      &.show{display: block;}
      grid-row: 2/span 1;
      dl.progress {
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-template-rows: 4rem;
        grid-gap: 1rem;
        align-items: center;
        justify-content: start;
        dt.dial {
          width: 7rem;
          &+dd{
            width: 3.5rem;
            position: relative;
          }
        } 
      }
      .data-treatment.bars{
        margin: 1rem 1rem 0 0;
      }
    }
    #big-progress-dial{
      grid-column: 2/span 1;
      grid-row: 2/span 1;
      --size: 10rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr;
      align-items: center;
      justify-content: center;
      justify-items: center;
      .slice{
        width: var(--size);
        height: calc(var(--size) / 10);
        background-image: url('../images/big-progress-dial-wedge-empty.svg');
        position: absolute;
        transform-origin: left center;
        transform: rotate(var(--rotate-angle));
        left: 50%;
        top: 50%;
        &.dt{background-image: url('../images/big-progress-dial-wedge-blue.svg');}
        &.fe{background-image: url('../images/big-progress-dial-wedge-beige.svg');}
        &.md{background-image: url('../images/big-progress-dial-wedge-orange.svg');}
        &.mc{background-image: url('../images/big-progress-dial-wedge-blue.svg');}
      }
    
    }
    #slices{
      position: relative;
      grid-column: 1/-1;
      grid-row: 1/-1;
      &.animate{
        animation-name: heartbeat;
        animation-duration: 1.5s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }
    }
    #big-stop-button{
      padding: 0;
      background: transparent;
      color: transparent;
      border: 0;
      outline: 0;
      width: 55%;
      grid-column: 1/-1;
      grid-row: 1/-1;
      cursor: pointer;
      margin-top: 1.2rem;
      svg:hover{
        transform: perspective(300px) scale(1.02) rotatex(-4deg);
      }
      &:active svg:hover{
        transform: perspective(200px) scale(1) rotatex(-2deg);
      }
    }
    #progress-text{
      font-size: 3rem;
      font-weight: 700;
      color:$dt-fg;
      font-size: 3rem;
      grid-column: 1/-1;
      grid-row: 1/-1;
      align-self: end;
      text-align: center;
    }
    #ongoing-panel{
      grid-column: 1/-1;
      display: flex;
      flex-direction: row;
      justify-content: stretch;
      align-items: flex-start;
    }
    #stages{
      dt{width: 7rem;}
      dd{width: 3.5rem;position: relative;}
      display: grid;
      grid-template-columns: 2fr 1fr;
      grid-template-rows: 4rem;
      grid-gap: 1rem;
      align-items:center;
      justify-content: start;
      @each $stage, $color in $stages {
        &.#{$stage}{
          &+#collector-rule{background-color: $color;}
          dt.#{$stage}+dd:after{
            content: '◀';
              color: $color;
              position: absolute;
              left: 110%;
              top: 40%;
              transform: scaley(1.2);
          }
          &~#current-progress{
            .#{$stage}-stats{
              display: block;
              margin-left: 2em;
              margin-top: 2em;
            }
          }
        }
      }
      &.fe ~ #current-progress .fe-stats{margin-top: 0;}
      &.md ~ #current-progress .md-stats{display: grid;}
    }
    #collector-rule{
      content: ' ';
      width: 0.2rem;
      height: 17rem;
      margin-left: 0.8rem;
    }
    #current-progress-dial{
      width: 10rem;
      margin-left: 2rem;
      margin-top: 3.5rem;
      @extend %dial;
      span{
        @extend %dial-text;
        font-size: 2rem;
      }
      svg{
        @extend %dial-svg;
      }
      &[data-value^='100'], &[data-value^='100%']{
        svg.dial ellipse{
          animation: none;
        }
      }
    
    }
    @each $stage, $color in $stages {
      #stages.#{$stage} ~ #current-progress-dial {
        span{color:$color;}
        svg{
          .filler{stroke: $color;}
          .blob{stroke: $color; fill: $color;}
        }
      }
    }
    #current-progress{
      flex-grow: 1;
      align-self: stretch;
      .current-progress-section{display: none;}
      
      .data-treatment{
        margin-left: 2em;
        margin-top: 2em;
      }

    }
  }
  &.started{
    #unstarted{display: none;}
    #started{display: grid;}
  }
}
#processor-stats{
  &.unavailable{display: none;}
  --chart-height: 100px;
  --chart-width: 300px;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: var(--chart-width);
  display: grid;
  grid-template-rows: repeat(5, 1.5rem var(--chart-height));
  grid-template-columns: 1fr;
  overflow: auto;
  padding: 1rem 1rem;
  max-height: calc(100vh - 8.6rem);
  background-color: #00000020;
  background-image: linear-gradient(to right, transparentize($black, 0.25) 0%, transparentize($black, 0.63) 5px, transparentize($black, 0.87) 10px, transparentize($black, 0.87) 100%);
  h3{
    color:$grey-main;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
    align-self: end;
  }
  svg{
    .c3-axis-y{
      line, path{stroke: $grey-main!important;}
      text{fill: $grey-main!important;}
    }
  }
  // display: none;
}
svg.dial{
  position: relative;
  .outline{stroke:#757575;stroke-width:3;stroke-linecap:square;fill:none}
  .filler{stroke:#1a83de;stroke-width:7;stroke-linecap:round;fill:none}
  .blob{stroke:#0091da;stroke-width:14;stroke-linecap:round;stroke-opacity:.5;fill:#0091da}
}

dt.dial{
  border-left: 4px solid;
  color: $ae-progress-fg;
  text-transform: uppercase;
  @include set-font-primary;
  font-size: 0.9em;
  font-weight: 600;
  text-overflow: ellipsis;
  overflow: hidden;
  &+dd{
    @extend %dial;
    span{
      @extend %dial-text;
    }
    svg.dial{
      @extend %dial-svg;
    }
  }
  &[data-value^='100'], &[data-value^='100%'], &[data-value='0'], &[data-value='0%']{
    &+dd svg.dial ellipse{
      animation: none;
    }
  }
  @each $stage, $color in $stages {
    &.#{$stage} {
      border-color: $color;
      &+dd{
        .filler{stroke: $color;}
        .blob{stroke: $color; fill: $color;}
      }
    }
  }
  padding-left: 4px;
}
.data-treatment.bars{
  display: block;
  font-size: 0.9em;
  .bar{
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 2fr 1fr;
    // grid-auto-flow: column;
    align-items: center;
    justify-content: stretch;
  }
  .title{
    text-transform: uppercase;
    grid-column: 1/span 1;
    grid-row: 1/span 1;
    justify-self: start;
    letter-spacing: 1%;
    letter-spacing: 0.3em;
    font-size: 1em;
  }
  .value{
    grid-column: 2/span 1;
    grid-row: 1/span 1;
    justify-self: end;
    font-size: 1em;
  }
  .progress-container{
    grid-column: 1/-1;
    grid-row: 2/span 1;
    height: 1em;
    background-color: $dt-progress-bar-bg;
    border-radius: 0.5em;
    border-bottom: 1px solid $orth-main;
    box-shadow: inset 0 2px 2px 1px #000;
    padding: 0 0.25em;
  }
  .progress{
    height: 0.5em;
    background-color: $dt-progress-bar-fg;
    margin-top: 0.25em;
    border-radius: .25em;
    transition: width 5s ease-in;
    #completed-panel-1 &{
      transition: none;
    }
  }
}
.feStats{
  #completed-panel-2 &{
    tr[progress]{display: none;}
  }
  h4{
    font-size: 1.1em;
    font-weight: 600;
    color: $fe-fg;
    text-transform: uppercase;
    @include set-font-secondary;
    margin-top: 1rem;
    cursor: pointer;
    &:first-of-type{margin-top: 0;}
    &+table{display: none;}
    &.running+table, &.show+table, &.current+table{display: table;}
    &:after{
      content: '';
      width: 1.5rem;
      height: 1.5rem;
      margin-left: 0.5rem;
      transform: translateY(0.3rem);
    }
    &.running:after{background-image: url(../images/icons/running.svg); display: inline-block;}
    &.completed:after{background-image: url(../images/icons/check.svg); display: inline-block;}
  }
  table{
    width: 100%;
  }
  h4.FeatureSelection+table{
    background-image: url(../images/icons/addition-indicator.svg);
    background-repeat: no-repeat;
    background-size: 2.4rem;
    background-position: 16.5rem 3.3rem;
    background-position: calc(var(--td-offset-left) + -1.35rem) calc(var(--td-offset-top) + -0.15rem);
    background-size: calc(var(--td-offset-height) * 2.15);
    position: relative;
  }
  tr.FeatureOptimization:not([progress]){
    display: none;
    .completed-panel &{
      display: initial;
    }
  }
  td{
    @include set-font-secondary;
    font-size: 0.8em;
    line-height: 2.1em;
    padding-left: 1em;
    &.name{
      width: 13rem;
      &[progress]{
        padding-left: 2rem;
        background-image: url(../images/icons/filter.svg);
        background-repeat: no-repeat;
        background-size: 0.9rem;
        background-position: 0.7rem 0.4rem;
      }
    }
    &.value.IV-FCDS-value, &.value.FS-FCDS-value {
      padding-bottom: 1rem;
    }
    &.value{
      width: 2rem;
      text-align: right;
      font-family: monospace;
      font-size: 0.9em;
      padding-right: 2rem;
    }
    &.progress-bar{
      width: auto;
      .channel{
        transform: translatey(2px);
        display: inline-block;
        width: 89%;
        height: 1em;
        position: relative;
        background-image: linear-gradient(to bottom, transparent 0%, transparent 45%, $grey-main 45%, $grey-main 55%, transparent 55%, transparent 100%);
        &:before,&:after{
          position: absolute;
          width: 3px;
          top: 0px;
          content: '';
          bottom: 0px;
        }
        &:before{
          left: 0;
          background-color: $fe-fg;
        }
        &:after{
          right: 0;
          background-color: $grey-main;
          width: 1px;
        }
        .progress{
          width: var(--progress-value);
          height: 3px;
          background-color: $fe-fg;
          position: absolute;
          top: 44%;
          transition: width 5s ease-in;
          #completed-panel-2 &{
            transition: none;
          }
        }
      }
      &[data-value="100%"] .channel:after, &[data-value="100"] .channel:after{
        background-color: $fe-fg;
        width: 3px;
      }
      .value{
        display: inline-block;
        width: 10%;
        margin-left: 1%;
      }
    }
  }
}
#md-stats{
  display: grid;
  grid-template-columns: 14rem 1fr;
  grid-template-rows: 1fr 2rem 2rem;
  grid-gap: 1rem;
  #progress-boxes{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 2rem);
    align-content: center;
    justify-content: start;
    grid-gap: 1rem 1.5rem;
    grid-auto-flow: column;
    .model{
      font-size: 0.8em;
      font-weight: 500;
      padding: 0.3em 0.5em;
      text-transform: uppercase;
      overflow: hidden;
      text-overflow: ellipsis;
      background-color: $md-summary-boxes-not-started-bg;
      color: $md-summary-boxes-not-started-fg;
      line-height: 1.1em;
      cursor: default;
      &.unavailable{
        background-color: $md-summary-boxes-not-started-bg;
        background-image: none;
        opacity: 0.2;
        visibility: hidden;
      }
      &.running{
        background-image: linear-gradient(to right, $md-summary-boxes-ready-bg 0%, $md-summary-boxes-ready-bg var(--progress-value), $md-summary-boxes-running-bg var(--progress-value), $md-summary-boxes-running-bg 100%);
      }
      &.ready{
        background-color:$md-summary-boxes-ready-bg;
        color: $md-summary-boxes-started-fg;
      }
    }
  }
  #tiny-boxes{
    display: grid;
    grid-template-columns: repeat(15,1fr);
    grid-template-rows: repeat(9,1.2rem);
    grid-column-gap: 0.6rem;
    grid-row-gap: 0.5rem;
    grid-auto-flow: column;
    .tiny-box{
      background-image: linear-gradient(to bottom right, $md-summary-boxes-started-fg 0%, $orth-main 100%);
      position: relative;
      &.running{
        background-image: linear-gradient(to bottom, $md-summary-boxes-running-bg 0%, $md-summary-boxes-running-bg var(--fill), $md-summary-boxes-ready-bg var(--fill), $md-summary-boxes-ready-bg 100%);
      }
      &.ready{
        background-color: $md-summary-boxes-ready-bg;
        background-image: none;
      }
    }
  }
  #md-legend{
    grid-column: 1/-1;
    grid-row: 2/span 1;
    display: flex;
    flex-direction: row;
    .legend{
      margin-right: 1rem;
    }
    .legend.state{
      width: 0.7rem;
      height: 1rem;
      margin-right: 0.4rem;
      background-image: linear-gradient(to bottom right, $md-summary-boxes-started-fg 0%, $orth-main 100%);
      &.running{
        background-image: linear-gradient(to bottom, $md-summary-boxes-running-bg 0%, $md-summary-boxes-running-bg 40%, $md-summary-boxes-ready-bg 41%, $md-summary-boxes-ready-bg 100%);
      }
      &.ready{
        background-color: $md-summary-boxes-ready-bg;
        background-image: none;
      }
    }

  }  
  #md-subtext{
    grid-column: 1/-1;
    grid-row: 3/span 1;
    display: flex;
    flex-direction: row;
    .label{
      margin-right: 0.5rem;
    }
    .separator{
      margin: 0 2rem;
    }
  }
}
#model-dev-complete-dialog{
  width: 36rem;
}
@keyframes heartbeat{
  0% {transform: scale(1); opacity: 0.8;}
  38% {transform: scale(1); opacity: 0.8;}
  40% {transform: scale(1.02); opacity: 1;}
  43% {transform: scale(1.01); opacity: 0.9;}
  48% {transform: scale(1.01); opacity: 0.9;}
  50% {transform: scale(1.02); opacity: 1;}
  53% {transform: scale(1); opacity: 0.8;}
  100% {transform: scale(1); opacity: 0.8;}
}
@keyframes heartbeatsvg{
  0% {fill-opacity: 0.5;stroke-opacity:0.5}
  45% {fill-opacity: 0.5;stroke-opacity:0.5}
  50% {fill-opacity: 1;stroke-opacity:1}
  60% {fill-opacity: 0.5;stroke-opacity:0.5}
  100% {fill-opacity: 0.5;stroke-opacity:0.5}
}
