#file-upload-dialog{
  --line-height: 1.1rem;
  width: 45rem;
  .content{
    display: flex;
    flex-direction: column;
    grid-column-gap: 2rem;
    grid-template-rows: repeat(7, 3.3rem);
    margin: 0 4rem;
  }
  label.main[for="fname-or-upload"]{
    .left{margin-right: 0.5rem;}
    .right{margin-left: 0.5rem;}
    display: flex;
    align-items: center;
  }
  .compact{
    margin-bottom: 1rem;
    &#file-connection-name{
      margin-bottom: 0;
    }
    &#loc-on-server{
      margin-top: 0.5rem;
    }
  }
  label.main[for='file-dev-or-prod']{
    grid-column: 1/span 2;
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    label{
      vertical-align: unset;
    }
    .slider{cursor: not-allowed;}
    .left, .right{margin: 0 0.5rem;}
  }
  .button-bar{
    grid-column: 1/span 2;
    display: flex;
    justify-content: center;
    margin-top: 1.5rem;
    margin-bottom: 2rem;
    #file-test-button{
      margin-right: 2rem;
      svg{
        width: 1.2rem; 
        path.success{fill: #0c4217;}
        path.fail{fill: #dd2c00;}
        path{transition: fill-opacity 0.3s ease-in-out;}
      }
      &.untested{
        path.success, path.fail{fill-opacity: 0;}
      }
      &.success{
        path.success{fill-opacity: 1;}
        path.fail{fill-opacity: 0;}
      }
      &.fail{
        path.success{fill-opacity: 0;}
        path.fail{fill-opacity: 1;}
      }
    }
  }
  #drop-area {
    height: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: stretch;
    margin-bottom: 2rem;
    --on-color: #{transparentize($primary-main, 0.85)};
    --off-color: #{transparentize($primary-main, 0.95)};
    --border-color: #{transparentize($primary-main, 0.5)};
    border: 1px dashed var(--border-color);
    background-image: repeating-linear-gradient(135deg, var(--off-color), var(--off-color) 35px, var(--on-color) 35px, var(--on-color) 70px);
    box-sizing: border-box;
    svg.cloud-icon {
      width: 7rem;
      align-self: center;
      margin: 1rem 3rem;
      transform: scale(1);
      transform-origin: center center;
      transition: transform 0.2s ease-in;
      path{fill: white;}
    }
    img.uploading{display: none;width: 6rem;}
    p.uploading{display: none;}
    p.dnd-message{margin: 1rem 0;}
    p.allowed-file-types{
      font-size: 0.7rem;
      margin-bottom: 0.4rem;
    }
    #file-browse-message{
      color: $primary-main;
      text-align: center;
      input{display: none;}
      cursor: pointer;
      span{
        @extend %icon-and-text-button;
        text-transform: capitalize;
        padding: 0.5rem 1rem;
      }
    }
    .status{display: none;}
    .status.success{margin-bottom: 1rem;}
    &.no-dnd{
      background: none;
      border: none;
      height: 10rem;
      svg{display: none;}
      p.dnd-message{display: none;}
    }
    &.is-dragover{
      --on-color: #{transparentize(white, 0.85)};
      --off-color: #{transparentize(white, 0.95)};
      --border-color: #{transparentize(white, 0.5)};
      svg{
        transform: scale(1.2);
      }
    }
    &.uploading{
      img.uploading, p.uploading{display: block;}
      svg.cloud-icon, p.allowed-file-types, p.dnd-message, label#file-browse-message {
        display: none;
      }
    }
    &.upload-successful{
      p.allowed-file-types, p.dnd-message, label#file-browse-message {
        display: none;
      }
      svg.cloud-icon{
        transform: scale(2);
        path{
          fill: $graph-color-2;
        }
      }
      .status.fail{display: none;}
      .status.success{
        display: block;
        .name{margin-left: 0.5rem;}
      }
    }
    &.upload-failed{
      p.allowed-file-types, p.dnd-message, label#file-browse-message {
        display: none;
      }
      svg.cloud-icon{
        transform: scale(1.5);
        path{
          fill: $graph-color-4;
        }
      }
      .status.success{display: none;}
      .status.fail{
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;
        justify-content: space-around;
        align-items: center;
        flex-grow: 2;
        .generic{
          font-weight: 400;
          text-transform: uppercase;
          @include set-font-secondary;
        }
      }
    }
  }
}

#main-content[current-page-marker='file-upload']:before{
  background-image: url(../images/icons/plug.svg);
}

#dialogs-active.on[current-dialog="file-upload-dialog"]{
  position: unset;
  grid-row: 3/-1;
  grid-column: 2/-1;
}