#dialogs-sleeping{ display: none; }
#dialogs-active{
  flex-direction: column;
  grid-column: 1/-1;
  grid-row: 1/-1;
  justify-content: center;
  align-items: center;
  z-index: -1;
  &.on{
    position: absolute;
    top: 5rem;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    display: flex;
  }
}
dialog, .box, .no-html5dialog dialog.dialog{
  flex-direction: column;
  align-items: stretch;
  padding: 0;
  background-color: $dialog-bg;
  background-image: linear-gradient(to bottom right, transparentize(black, 1), transparentize(black, 0.1) 80%);
  color: $dialog-fg;
  box-shadow: 0 2.3rem 4rem -2rem black;;
  border: 0;
  max-height: 95vh;
  min-height: 20vh;
  min-width: 30rem;
  &>*{margin: 0 2rem;}
  .title{
    margin: 1rem 0;
    padding: 0 1rem;
    background: transparent;
    color: $dialog-title-fg;
    font-family: Montserrat;
    flex-basis: 3rem;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    span.titleText{
      font-size: 1.2rem;
      font-weight: 900;
      text-transform: uppercase;
      letter-spacing: 0.4rem;
      line-height: 2rem;
    }
  }
  .content{
    overflow: auto;
    background-color: transparent;
    flex-grow: 1;
  }
  &::backdrop{
    position: fixed;
    top:0;right:0;bottom:0;left: 0;
    background-color: rgba(0,0,0,0.05);
  }
  .button-bar{
    text-align: center;
    margin: 0.5rem 1rem 1rem 1rem;
    padding: 0.5rem 0 0 0;
  }
}
#dialogs-active dialog.dialog{
  animation-name: dlg-slide-in;
  animation-duration: 0.3s;
  animation-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
}
dialog .title{position: relative;}
dialog button.close{
  &:after{
    content: '\0292B';
    font-size: 1rem;
  }
  padding: 0;
  background: transparent;
  color: $dialog-close-button-fg;
  position: absolute;
  right: 1.8rem;
  top: 50%;
  transform: translateY(-50%);
  box-shadow: none;
  cursor: pointer;  
  border: 0;
  outline: 0;
  &:hover, &:active, &:focus{
    color: $dialog-close-button-highlight-color;
  }
}

dialog[open], .box{
  display: flex;
}

@keyframes dlg-slide-in{
  0%{transform: translateY(-100%);}
  90%{transform: translateY(20%);}
  97%{transform: translateY(-5%);}
  100%{transform: translateY(0);}
}

