#new-db-connection-dialog{
  --line-height: 1.1rem;
  width: 45rem;
  .content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 2rem;
    grid-template-rows: repeat(7, 3.3rem);
  }
  label.on-off[for='existing-db']{
    grid-row: 1/span 1;
    grid-column: 1/span 1;
    align-self: center;
  }
  label.on-off[for='new-connection-db']{
    grid-row: 2/span 1;
    grid-column: 1/span 2;
    align-self: center;
  }
  label.compact{
    align-self: start;
    margin-top: 0;
  }
  label.main[for='dev-or-prod']{
    grid-column: 1/span 2;
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    label{
      vertical-align: unset;
    }
    .slider{cursor: not-allowed;}
    .left, .right{margin: 0 0.5rem;}
  }
  .button-bar{
    grid-column: 1/span 2;
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
    #test-button{
      margin-right: 2rem;
      svg{
        width: 1.2rem; 
        path.success{fill: #0c4217;}
        path.fail{fill: #dd2c00;}
        path{transition: fill-opacity 0.3s ease-in-out;}
      }
      &.untested{
        path.success, path.fail{fill-opacity: 0;}
      }
      &.success{
        path.success{fill-opacity: 1;}
        path.fail{fill-opacity: 0;}
      }
      &.fail{
        path.success{fill-opacity: 0;}
        path.fail{fill-opacity: 1;}
      }
    }
  }
}

#main-content[current-page-marker='db-connection']:before{
  background-image: url(../images/icons/plug.svg);
}

#dialogs-active.on[current-dialog="new-db-connection-dialog"]{
  position: unset;
  grid-row: 3/-1;
  grid-column: 2/-1;
}