body{
  --table-row-height: 2.5rem;
  --table-col-width: 15rem;
  --table-no-cols: 4;
}
.scrollable-table{
  display: flex;
  flex-direction: column;
  max-height: 98%;
  justify-content: stretch;
  overflow-x: auto;
  border: 1px solid #d7dfe6;
  box-shadow: 6px 6px 30px -10px #0000007a;

  th, td{padding-left: 0.5rem;}
  thead{
    flex-grow: 0;
    th{
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
    }
  
  }
  tbody{
    flex-grow: 1;
    overflow-y: auto;
  }
  thead, tbody{
    tr{
      display: grid;
      grid-template-columns: repeat(var(--table-no-cols), var((--table-col-width)));
      grid-template-rows: var(--table-row-height);
    }
  }
}