#main-content[current-page-marker='md_auto-code']:before{
  background-image: url('../images/icons/download.svg');
}
#main-content[current-page-marker='md_auto-code']{
  .content.md-auto-code{
    display: flex; 
    flex-direction: row;
    justify-content: stretch;
    align-content: stretch;
    z-index: 1;
    height: calc(100vh - 8.6rem);
  }
}

#md-outer-container{
  --code-area-width: 40vw;
  --code-area-delta : 0;
  display: grid;
  grid-template-rows: 100%;
  grid-template-columns: minmax(400px, 1fr) 10px calc(var(--code-area-width) - (var(--code-area-delta) * 1px));
  width: 100%;
  height: 100%;
  .separator{
    background-color: $mp-separator-bg;
    position: relative;
    cursor: col-resize;
  }
}

#api{
  background-color: #1e2125;//$mp-graph-area-bg;
  height:100%;
  overflow: hidden;
  .tabs-container {
    display: grid;
    grid-template-columns: calc(var(--code-area-width) - (var(--code-area-delta) * 1px));
    grid-template-rows: 3rem 1fr;
    align-items:  stretch;
    justify-items: stretch;
    justify-content: stretch;
    align-content: stretch;
  }
  .tab-trigger{
    &.template{
      display: none;
    }
    padding: 0.6rem 1.4rem 0.3rem;
    background-color: transparentize(black, 0.4);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    margin: 0 0 0 1rem;
    transform: scale(0.8);
    transform-origin: center bottom;
    transition: transform 0.1s;
    &.selected{
      background-color: transparentize(black, 0.7);
      transform: none;
    }
  }
  .tabs-content-container {
      // background-color: transparentize(black, 0.7);
      grid-column: 1/-1;
      grid-row: 2/span 1;
      height: calc(100vh - 11.6rem);
  }

  .tab-input:checked+.tab-content {
      display: block;
  }

  .tab-content {
    margin: 0 1rem;
    color: $orth-light;
    height: 100%;
    overflow: auto;
  }

  .tabsTriggersContainer {
    grid-column: 1/-1;
    grid-row: 1/span 1;
    border-bottom: 2px solid transparentize(black, 0.7);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
    background-color: #2f3339;
  } 
  pre, code{
    @include set-font-code;
  }
  code.hljs{
    background-color:transparent;
    overflow: initial;
  }
}
#auto-code-generator-main{
  width: 100%;
}
#auto-code-generator{
  
  .searchBar{
    margin: 1rem 0 0 2rem;
    select{
      @include set-font;
      font-size: 1rem;
    }
    label{
      @include set-font;
      font-size: 0.9rem;
      display: block;
      span{
        margin-right: 1rem;
      }
    }
    input{
      outline: 0;
      border: 1px solid #3a3e4363;
      padding: 5px 7px 5px 30px;
      background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.54), rgba(255, 255, 255, 0.53)), url(../images/icons/search.svg);
      background-repeat: no-repeat;
      background-position: 5px 3px;
      background-size: 10%;
      font-size: 1rem;
      color: #3a3e43;
    }
  }
  .tableContainer {
    margin: 1rem 2rem 0 2rem;
    h4{
      background-color: $mp-table-caption-bg;
      color: $mp-table-caption-fg;
      padding: 1.5rem 1.3rem;
      font-weight: 600;
    }
    table{
      width: 100%;
      border-spacing: 0 5px;
      border-collapse: separate;
      th{
        background-color: $mp-table-caption-bg;
        color: $mp-table-caption-fg;
        padding: 1rem;
        text-align: left;
        &.name{
          text-align: left;
        }
      }
      tr.trophy{
        td{
          background-color: $secondary-light;
          color: white;
          &.dPath{
            background-size: calc(var(--icon-size) - .8rem);
            background-position: 83% calc((3.7rem - var(--icon-size)) / 2);
            background-image: url('../images/icons/trophy.svg');
            a{
              background-image: url('../images/icons/cloud-download-grey.svg');
            }
          }
        }
      }
      tr.selected td{
        background-color: $primary-light;
        color: white;
        &.dPath{
          &:after{
            color: $mp-separator-bg;
            transform: translate(2px, calc(var(--table-row-height) / 2 - 6px))  scaley(1.5);
          }
          a{
            background-image: url('../images/icons/cloud-download-grey.svg');
          }
        }

      }
      td{
        --icon-size: 2.5rem;
        padding: 1rem;
        &.name{
          padding: 1rem 1rem 1rem 4rem;
        }

        background-size: var(--icon-size);
        background-position: calc((4rem - var(--icon-size)) / 2);
        background-repeat: no-repeat;
        background-color: $mp-row-bg;
        position: relative;
        transition: all 0.3s ease-out;
        &.type-spark{
          background-image: url('../images/icons/spark.svg');
        }
        &.type-python{
          background-image: url('../images/icons/python.svg');
        }
        &.type-h2o{
          background-image: url('../images/icons/h2o.svg');
        }
        &.dPath{
          &:after{
            content: '◀';
            display: block;
            color: $mp-separator-bg;
            transition: color 0.3s ease-out, transform 0.3s ease-out;
            position: absolute;
            right: -2rem;
            top: 0px;
            transform: translate(2px, calc(var(--table-row-height) / 2 - 6px)) scaley(0);
          }
          a{
            width: var(--icon-size);
            height: var(--icon-size);
            display: block;
            background-image: url(../images/icons/cloud-download.svg);
            position: absolute;
            left: calc(var(--icon-size) *1.5);
            top: calc( (var(--table-row-height) - var(--icon-size)) / 2 + 5px);
            --icon-size: 1.7rem;
            background-repeat: no-repeat;
          }
        }
      }
      &[data-active-source='test'] tr.train{
        display: none;
      }
      &[data-active-source='train'] tr.test{
        display: none;
      }
    }
  }

}